<template>
    <div class="order-info" v-loading="loading" v-if="orderInfo">
        <div class="content-top">
            <p class="order-sn">{{ $L('订单编号') }}：{{ orderInfo.orderSn }}</p>
            <step :order-info="orderInfo"></step>
        </div>
        <div class="order-details" v-if="orderInfo.orderType == 1 || orderInfo.orderType == 4">
            <div class="order-left">
                <p class="title">{{ $L('订单信息') }}</p>
                <order-detail v-bind:orderInfo="orderInfo"></order-detail>
            </div>
            <div class="order-right">
                <div class="right-content">
                    <p class="title">{{ $L('物流信息') }}</p>
                    <div class="deliver" v-if="deliveryList.length==0">
                        {{ $L('暂时未有物流信息') }}
                    </div>
                    <div v-if="deliveryList.length>0">
                        <div class="order-number" v-if="orderInfo.deliveryName != '' || orderInfo.deliveryNum != ''">
                            {{ orderInfo.deliveryName }} {{ orderInfo.deliveryNum }}
                        </div>
                        <logistics-info v-bind:deliveryList="deliveryList"></logistics-info>
                    </div>
                </div>
            </div>
        </div>
        <div class="leave-msg" v-if="orderInfo.orderType == 3">
            <p class="title">{{ $L('商家留言') }}</p>
            <p>{{ orderInfo.adminMemo ? orderInfo.adminMemo : $L('无') }}</p>
        </div>
        <div class="prod-info" v-if="orderInfo">
            <prod-list :orderInfo="orderInfo"></prod-list>
        </div>
        <bottom-btn :order-info="orderInfo" @showLoad="showLoad"></bottom-btn>
    </div>
</template>

<script>
    import OrderDetail from "../../../components/order/orderDetail";
    import LogisticsInfo from "../../../components/order/LogisticsInfo";
    import ProdList from "../../../components/order/prodList";
    import BottomBtn from "../../../components/order/bottomBtn";
    import Step from "../../../components/order/step";
    export default {
        name: "orderInfo",
        components: {ProdList, LogisticsInfo, OrderDetail,BottomBtn,Step},
        data() {
            return {
                loading: true,
                orderInfo: null,
                deliveryList:[]
            }
        },
        beforeRouteUpdate(to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
            // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
            // 可以访问组件实例 `this`
            let id = 0;
            if (to.params.hasOwnProperty('id')) {
                id = to.params.id;
                id = parseInt(id);
            }
            if (!isNaN(id) && id) {
                this.getData(id);
            } else {
                this.showError();
            }
            next();
        },
        created() {
            let id = 0;
            if (this.$route.params.hasOwnProperty('id')) {
                id = this.$route.params.id;
                id = parseInt(id);
            }
            if (!isNaN(id) && id) {
                this.getData(id);
            } else {
                this.showError();
            }
        },
        methods: {
            backFunc() {
                this.$router.push({name:'orderList',params:{type:'all'}});
            },
            showError() {
                this.$alert(this.$L('订单不存在或已删除'), this.$L('参数有误'), {
                    confirmButtonText: this.$L('返回'),
                    type: 'error',
                }).then(() => {
                    this.backFunc();
                });
            },
            getData(id) {
                const _self = this;
                _self.loading = true;
                _self.$api('orderDetail', {orderId: id}, function (reData) {
                    if (reData.flag === 1) {
                        _self.orderInfo = reData.data;
                        _self.deliveryList = reData.data.deliveryList;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('重试'),
                            cancelButtonText: _self.$L('返回'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData(id);
                        }).catch(action => {
                            _self.backFunc();
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('重试'),
                        cancelButtonText: _self.$L('返回'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData(id);
                    }).catch(action => {
                        _self.backFunc();
                    });
                });
            },
            gotoGoodsPage(item) {

            },
            showLoad(type) {
                if (typeof type === "undefined" || type) {
                    this.loading = this.$loading({lock: true});
                } else {
                    if (this.loading) {
                        this.loading.close();
                        this.loading = null;
                    }
                }
            },

        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common";

    .order-info {
        position: relative;
        width: 100%;
        min-height: 500px;

        .content-top {
            padding: 20px;
            background: #fff;

            .order-sn {
                font-size: 14px;
            }

            .steps {
                margin-top: 20px;
            }
            .end{
                font-size: 16px;
                background-color: #f2f2f2;
                padding: 20px;
                margin-top: 10px;
            }
        }

        .order-details {
            margin-top: 10px;

            .title {
                font-weight: 700;
                margin-bottom: 10px;
                font-size: 14px;
            }

            .order-left {
                width: calc((100% - 10px) / 2);
                background: #fff;
                display: inline-block;
                padding: 20px;
                height: 360px;
                vertical-align: top;
            }

            .order-right {
                width: calc((100% - 10px) / 2);
                background: #fff;
                display: inline-block;
                margin-left: 10px;
                padding: 20px;
                height: 360px;
                vertical-align: top;
                .order-number {
                    margin-bottom: 10px;
                    line-height: 42px;
                }
                .right-content {
                    overflow-x: hidden;
                    .logistics-info{
                        height: 230px;
                        overflow-y: scroll;
                    }
                }
            }
        }

        .leave-msg{
            background: #fff;
            padding: 20px;
            margin-top: 10px;
            .title{
                font-weight: 700;
                margin-bottom: 10px;
                font-size: 14px;
            }
        }
        .prod-info {
            margin-top: 10px;
        }
    }
</style>
<style>
    .el-message-box {
        width: 420px !important;
    }
</style>