<template>
    <div class="step" v-if="orderInfo">
        <div v-if="orderInfo.orderType == 2">
            <el-steps :active="spellFunc(orderInfo.pStatus)" finish-status="success" class="steps" v-if="orderInfo.pStatus !== -1 && orderInfo.pStatus !== 3">
                <el-step :title="$L('拍下商品')" :description="orderInfo.addTime"></el-step>
                <el-step :title="$L('凑单中')"></el-step>
                <el-step :title="$L('订单支付')" :description="orderInfo.orderPayTime"></el-step>
                <el-step :title="$L('确认收货')" :description="orderInfo.orderConfirmTime"></el-step>
                <el-step :title="$L('订单评价')" :description="orderInfo.orderCommentTime"></el-step>
            </el-steps>
            <div class="end" v-else>
                {{$L('拼团已经结束了，下次再来参与咯。')}}
            </div>
        </div>
        <el-steps :active="orderInfo.orderStep" finish-status="success" class="steps" v-else>
            <el-step :title="$L('提交订单')" :description="orderInfo.addTime"></el-step>
            <el-step :title="$L('订单支付')" :description="orderInfo.orderPayTime"></el-step>
            <el-step :title="$L('确认收货')" :description="orderInfo.orderConfirmTime"></el-step>
            <el-step :title="$L('订单评价')" :description="orderInfo.orderCommentTime"></el-step>
        </el-steps>
    </div>
</template>

<script>
    export default {
        name: "step",
        props:{
            orderInfo:{
                type:Object,
                default:()=>{
                    return {};
                }
            }
        },
        methods:{
            spellFunc(val){
                let x;
                switch (val) {
                    case -1:
                    case -3:
                        x = -1;
                        break;
                    case 1:
                    case 2:
                        x = 2;
                        break;
                    case 4:
                        x = 3;
                        break;
                    case 5:
                        x = 4;
                        break;
                    case 6:
                        x = 5;
                        break;
                }
                return x;
            },
        }
    }
</script>

<style lang="scss" scoped>
    .step{
        margin-top: 20px;
    }
</style>